@forward 'base';
@forward 'components';
@forward 'layout';
@forward 'pages';

.welcome-box {
  position: relative;
  border-radius: 10px;
  background-color: rgba(203, 203, 203, 0.09);
  padding: 20px;
  margin: 20px 0;
  border: 1px solid rgba(200, 200, 200, 0.2);

  .close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    border: none;
    background: none;
    font-size: 20px;
    cursor: pointer;
    color: #999999;

    &:hover {
      color: #ff0000;
    }
  }

  h3 {
    margin-top: 0;

    .icon {
      color: #d51e1e;
      margin-right: 10px;
    }
  }

  p {
    margin-bottom: 0;
  }
}
