@mixin styles {
  color-scheme: dark;

  /* Base Glass Background */
  --main-bg: rgba(24, 24, 27, 0.75);
  --mask-bg: rgba(30, 30, 33, 0.65);
  --main-border-color: rgba(255, 255, 255, 0.06);

  /* Text */
  --text-color: #f5f5f5;
  --text-muted-color: #a0a0a0;
  --text-muted-highlight-color: #bbbbbb;
  --heading-color: #ff6b6b;
  --label-color: #cfcfcf;

  /* Links */
  --link-color: #ff4d4f;
  --link-underline-color: rgba(255, 77, 79, 0.3);

  /* Buttons */
  --button-bg: rgba(255, 255, 255, 0.06);
  --btn-border-color: rgba(255, 255, 255, 0.08);
  --btn-backtotop-color: #ffffff;
  --btn-backtotop-border-color: rgba(255, 255, 255, 0.12);

  /* Cards */
  --card-bg: rgba(35, 35, 40, 0.7);
  --card-header-bg: rgba(45, 45, 50, 0.6);
  --card-hovor-bg: rgba(255, 77, 79, 0.1);
  --card-shadow: none;

  /* Sidebar */
  --sidebar-bg: rgba(30, 30, 35, 0.7);
  --sidebar-border-color: rgba(255, 255, 255, 0.04);
  --sidebar-muted-color: #aaaaaa;
  --sidebar-active-color: #ffffff;
  --sidebar-hover-bg: rgba(255, 77, 79, 0.1);
  --sidebar-btn-bg: rgba(255, 255, 255, 0.05);
  --sidebar-btn-color: #bbbbbb;

  /* Avatar */
  --avatar-border-color: rgba(255, 255, 255, 0.4);

  /* Topbar */
  --topbar-bg: rgba(24, 24, 27, 0.5);
  --topbar-text-color: #eeeeee;

  /* Input */
  --search-border-color: rgba(255, 255, 255, 0.1);
  --search-icon-color: #999999;
  --input-focus-border-color: #ff4d4f;

  /* Tags */
  --tag-hover: rgba(255, 77, 79, 0.15);
  --checkbox-color: #888888;
  --checkbox-checked-color: #ff4d4f;

  /* Post list */
  --post-list-text-color: #eaeaea;

  /* Pagination */
  --btn-patinator-text-color: #ffffff;
  --btn-paginator-hover-color: rgba(255, 77, 79, 0.08);

  /* TOC */
  --toc-highlight: #ff4d4f;
  --toc-popup-border-color: rgba(255, 255, 255, 0.08);

  /* Tables */
  --tb-odd-bg: rgba(40, 40, 45, 0.5);
  --tb-even-bg: rgba(32, 32, 36, 0.5);
  --tb-border-color: rgba(255, 255, 255, 0.08);

  /* Footnote */
  --footnote-target-bg: rgba(255, 77, 79, 0.25);

  /* Share button */
  --btn-share-color: #aaaaaa;
  --btn-share-hover-color: #ffffff;

  /* Shimmer/Loading */
  --shimmer-bg: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 77, 79, 0.3) 50%,
    rgba(255, 255, 255, 0) 100%
  );

  /* Prompt */
  --prompt-text-color: rgba(255, 255, 255, 0.85);
  --prompt-tip-bg: rgba(255, 77, 79, 0.12);
  --prompt-tip-icon-color: #ff4d4f;
  --prompt-info-bg: rgba(40, 100, 140, 0.8);
  --prompt-info-icon-color: #69b8ff;
  --prompt-warning-bg: rgba(140, 100, 20, 0.88);
  --prompt-warning-icon-color: rgba(255, 200, 0, 0.9);
  --prompt-danger-bg: rgba(130, 30, 30, 0.8);
  --prompt-danger-icon-color: #ee3333;

  /* Tags / Dash */
  --tag-border: rgba(255, 77, 79, 0.5);
  --tag-shadow: rgba(0, 0, 0, 0.1);
  --dash-color: #5a5a5a;
  --search-tag-bg: rgba(60, 60, 65, 0.6);

  /* Categories */
  --categories-border: rgba(255, 255, 255, 0.08);
  --categories-hover-bg: rgba(255, 77, 79, 0.1);
  --categories-icon-hover-color: #ffffff;

  /* Archive */
  --timeline-node-bg: rgba(255, 255, 255, 0.6);
  --timeline-color: rgba(255, 255, 255, 0.3);
  --timeline-year-dot-color: var(--timeline-color);

  /* Code blocks */
  --language-border-color: #2d2d2d;
  --highlight-bg-color: #1a1a1a;
  --highlighter-rouge-color: #ffffff;
  --highlight-lineno-color: #666666;
  --inline-code-bg: #2d2d2d;
  --code-color: #ffffff;
  --code-header-text-color: #999999;
  --code-header-muted-color: #2d2d2d;
  --code-header-icon-color: #666666;
  --clipboard-checked-color: #28a745;
  --filepath-text-color: #ffffff;

  /* Other global effects */
  border-radius: 10px;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);

  /* Force hide light style in dark mode */
  .light {
    display: none;
  }

  /* UI elements */
  .categories.card,
  .list-group-item {
    background-color: var(--card-bg);
    border-radius: 10px;
    backdrop-filter: blur(10px);
  }

  .categories {
    .card-header {
      background-color: var(--card-header-bg);
    }

    .list-group-item {
      border-left: none;
      border-right: none;
      padding-left: 2rem;
      border-color: var(--categories-border);

      &:last-child {
        border-bottom-color: var(--card-bg);
      }
    }
  }

  #archives li:nth-child(odd) {
    background-image: linear-gradient(
      to left,
      rgba(30, 30, 35, 0.6),
      rgba(50, 50, 55, 0.6),
      rgba(50, 50, 55, 0.6),
      rgba(30, 30, 35, 0.6)
    );
  }

  #disqus-thread {
    color-scheme: none;
  }
}
