@mixin styles {
  color-scheme: light;

  /* Framework color */
  --main-bg: rgba(255, 255, 255, 0.85);
  --mask-bg: rgba(245, 245, 245, 0.9);
  --main-border-color: rgba(0, 0, 0, 0.06);

  /* Common color */
  --text-color: #222222;
  --text-muted-color: #666666;
  --text-muted-highlight-color: #999999;
  --heading-color: #d9001b;
  --label-color: #555555;
  --blockquote-border-color: rgba(255, 77, 79, 0.3);
  --blockquote-text-color: #555555;
  --link-color: #ff4d4f;
  --link-underline-color: rgba(255, 77, 79, 0.2);
  --button-bg: rgba(255, 255, 255, 0.7);
  --btn-border-color: rgba(0, 0, 0, 0.05);
  --btn-backtotop-color: #333333;
  --btn-backtotop-border-color: rgba(0, 0, 0, 0.08);
  --checkbox-color: #cccccc;
  --checkbox-checked-color: #ff4d4f;
  --img-bg: radial-gradient(circle, #ffffff 0%, #f0f0f0 100%);
  --shimmer-bg: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 77, 79, 0.2) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  --card-bg: rgba(255, 255, 255, 0.9);
  --card-hovor-bg: rgba(255, 77, 79, 0.08);
  --card-shadow: none;
  --kbd-wrap-color: #bbbbbb;
  --kbd-text-color: #222222;
  --kbd-bg-color: rgba(0, 0, 0, 0.03);
  --prompt-text-color: #333333;
  --prompt-tip-bg: rgba(255, 77, 79, 0.1);
  --prompt-tip-icon-color: #ff4d4f;
  --prompt-info-bg: #e8f4fa;
  --prompt-info-icon-color: #0075d1;
  --prompt-warning-bg: #fff8e1;
  --prompt-warning-icon-color: #f39c12;
  --prompt-danger-bg: rgba(255, 205, 210, 0.7);
  --prompt-danger-icon-color: #df3c30;

  /* Tags */
  --tag-border: rgba(0, 0, 0, 0.08);
  --tag-shadow: rgba(0, 0, 0, 0.03);
  --search-tag-bg: rgba(255, 255, 255, 0.75);

  /* Categories */
  --categories-border: rgba(0, 0, 0, 0.08);
  --categories-hover-bg: rgba(255, 77, 79, 0.05);
  --categories-icon-hover-color: #000000;

  /* Archive */
  --timeline-color: rgba(0, 0, 0, 0.08);
  --timeline-node-bg: #d6d6d6;
  --timeline-year-dot-color: #ffffff;

  /* Code highlight */
  --language-border-color: #e5e5e5;
  --highlight-bg-color: #ffffff;
  --highlighter-rouge-color: #000000;
  --highlight-lineno-color: #999999;
  --inline-code-bg: #f8f8f8;
  --code-color: #000000;
  --code-header-text-color: #666666;
  --code-header-muted-color: #e5e5e5;
  --code-header-icon-color: #999999;
  --clipboard-checked-color: #28a745;

  /* Extra */
  border-radius: 10px;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);

  .dark {
    display: none;
  }
}

/* 关键字、名称 */
// 关键字：int, return
.highlight .n {
  // 名称：main, std
  color: inherit !important;
}


/* 字符串：例如 "Hello, World!" */
.highlight .s {
  color: #de0877 !important;
  background: none !important;
}
