@use '../abstracts/variables' as v;
@use '../abstracts/mixins' as mx;
@use '../abstracts/breakpoints' as bp;
@use '../abstracts/placeholders';

$btn-border-width: 3px;
$btn-mb: 0.5rem;
$sidebar-display: 'sidebar-display';

#sidebar {
  @include mx.pl-pr(0);

  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: v.$sidebar-width;
  overflow-y: auto;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border-right: 1px solid var(--sidebar-border-color);
  transition: background 0.3s ease, transform 0.3s ease;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  @include bp.lt(bp.get(lg)) {
    @include mx.slide;

    transform: translateX(-#{v.$sidebar-width});
    -webkit-transform: translateX(-#{v.$sidebar-width});

    [#{$sidebar-display}] & {
      transform: translateX(0);
    }
  }

  @include bp.xxxl {
    width: v.$sidebar-width-large;
  }

  a {
    @extend %sidebar-links;

    transition: color 0.2s ease;
  }

  #avatar {
    display: block;
    width: 6.5rem;
    height: 6.5rem;
    overflow: hidden;
    border-radius: 50%;
    box-shadow: 0 0 0 2px var(--avatar-border-color);
    transform: translateZ(0);

    @include bp.sm {
      width: 7rem;
      height: 7rem;
    }

    img {
      transition: transform 0.5s;

      &:hover {
        transform: scale(1.15);
      }
    }
  }

  .profile-wrapper {
    @include mx.mt-mb(2.5rem);

    padding-left: 2rem;
    padding-right: 1rem;
    width: 100%;

    @include bp.lg {
      margin-top: 3rem;
    }

    @include bp.xxxl {
      margin-top: 3.5rem;
      margin-bottom: 2.5rem;
      padding-left: 3rem;
    }
  }

  .site-title {
    @extend %clickable-transition;

    font-family: inherit;
    font-weight: 900;
    font-size: 1.75rem;
    line-height: 1.2;
    margin-top: 1.25rem;
    margin-bottom: 0.5rem;
    color: var(--site-title-color);

    &:hover {
      color: var(--link-color);
    }
  }

  .site-subtitle {
    font-size: 0.95rem;
    color: var(--site-subtitle-color);
    margin-top: 0.25rem;
    user-select: none;
  }

  ul {
    margin-bottom: 2rem;

    li.nav-item {
      @include mx.pl-pr(1.5rem);

      opacity: 0.9;

      @include bp.xxxl {
        @include mx.pl-pr(2.5rem);
      }

      &:not(:first-child) {
        margin-top: 0.25rem;
      }

      a.nav-link {
        display: flex;
        align-items: center;
        padding: 0.6rem;
        border-radius: 0.75rem;
        font-weight: 600;
        transition: background 0.25s ease, color 0.25s ease;

        &:hover {
          background-color: rgba(255, 77, 79, 0.08);
          color: var(--sidebar-active-color);

          i {
            color: var(--link-color);
          }
        }

        i {
          font-size: 0.95rem;
          margin-right: 1.25rem;
          opacity: 0.8;
          transition: color 0.25s ease;
        }

        span {
          font-size: 0.9rem;
          letter-spacing: 0.2px;
        }
      }

      &.active .nav-link {
        color: var(--link-color);
        background-color: rgba(255, 77, 79, 0.12);

        i {
          color: var(--link-color);
        }
      }
    }
  }

  .sidebar-bottom {
    padding-left: 2rem;
    padding-right: 1rem;
    margin-bottom: 1.5rem;

    @include bp.xxxl {
      padding-left: 2.75rem;
      margin-bottom: 1.75rem;
    }

    $btn-size: 1.75rem;

    %button {
      width: $btn-size;
      height: $btn-size;
      margin-bottom: $btn-mb;
      border-radius: 50%;
      color: var(--sidebar-btn-color);
      background-color: var(--sidebar-btn-bg);
      display: flex;
      align-items: center;
      justify-content: center;
      transition: background 0.25s ease, color 0.25s ease;

      &:not(:focus-visible) {
        box-shadow: 0 0 0 1px var(--sidebar-border-color);
      }

      &:hover {
        background-color: rgba(255, 77, 79, 0.1);
        color: var(--link-color);
      }
    }

    a {
      @extend %button;

      &:not(:last-child) {
        margin-right: v.$sb-btn-gap;

        @include bp.xxxl {
          margin-right: v.$sb-btn-gap-lg;
        }
      }
    }

    i {
      line-height: $btn-size;
    }

    #mode-toggle {
      @extend %button;
    }

    .icon-border {
      background-color: var(--sidebar-btn-color);
      width: $btn-border-width;
      height: $btn-border-width;
      border-radius: 50%;
      margin-bottom: $btn-mb;

      @include bp.xxxl {
        margin-left: calc((v.$sb-btn-gap-lg - $btn-border-width) / 2);
        margin-right: calc((v.$sb-btn-gap-lg - $btn-border-width) / 2);
      }
    }
  }
}

[#{$sidebar-display}] {
  #main-wrapper {
    @include bp.lt(bp.get(lg)) {
      transform: translateX(v.$sidebar-width);
    }
  }
}
